<template>
  <div id="custom_report_page">
    <div class="wrapper df dfv">
      <el-tabs v-model="curView" @tab-click="changeTab">
        <el-tab-pane v-for="(item, index) in sceneList" :key="index" :label="item.name" :name="index + ''"></el-tab-pane>
      </el-tabs>
      <div class="page-view">
        
        <div :id="'component-' + item.dId" 
          class="component"
          :style="calcComponentsStyle(item)"
          v-for='item in componentList' :key = "item.dId">
          <span>
            x:{{item.pos.x}}&nbsp;
            y:{{item.pos.y}}<br/>
            w:{{item.size.width}}&nbsp;
            h:{{item.size.height}}<br/>
          </span>
          <a style="position:absolute; top: 3px;right:8px;" @click="deleteComponent(item)">&times;</a>
          
        </div>

      </div>
      <div class="stand-by-panel">
        <div class="component"
          @mousedown.left="startDragFromStandByPanel(arguments[0])" >              
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
      globleConfig: {
        row: 24,
        column: 20,
        contentWidth: 0,
        contentHeight: 0,
      },
      curView: '0',
      lastView: '0',
      componentList: [],
      sceneList: new Array(10).fill('').map((v, i) => ({
        name: '页面' + (i + 1)
      })),

    }
  },
  computed: {
    
    gridWidth () {
      return this.globleConfig.contentWidth / this.globleConfig.column;
    },
    gridHeight () {
      return this.globleConfig.contentHeight / this.globleConfig.row;
    }
  },
  methods: {
    
    calcComponentsStyle (component) {
      let style = ''
      switch (component.state) {
        case 'standBy': {
          
          break;
        }
        case 'inPage': {
          style += `z-index: ${component.zIndex};`
          break;
        }
        case 'transforming': {
          style += 'opacity: 0.7;'
          style += (component.valid || component.isDropping) ? '' : 'background: rgba(255,36,0, 0.5);';
          style += `z-index: ${component.zIndex};`
          break;
        }
        case 'dropZone': {
          style += `z-index: ${component.zIndex};`
          style += 'background: rgba(102, 170, 102, 0.5);';
          break;
        }
      }
      return style;
    },
    updateComponent (component) {
      const traversal = c => {
        c._dom.style.webkitTransform =
        c._dom.style.transform =
            'translate(' + c.pos.x * this.gridWidth + 'px, ' + c.pos.y * this.gridHeight + 'px)';
        c._dom.style.width  = c.size.width * this.gridWidth + 'px';
        c._dom.style.height = c.size.height * this.gridHeight + 'px';
        c.children.forEach(traversal);
      }
      traversal(component)
    },
    conflictCheck (component) {
      let bottom = {
        pos: { x: 0, y: this.globleConfig.row },
        size: { width: this.globleConfig.column, height: 100 }
      }
      let right = {
        pos: { x: this.globleConfig.column, y: 0 },
        size: { width: 100, height: this.globleConfig.column }
      }
      let top = {
        pos: { x: 0, y: -100 },
        size: { width: this.globleConfig.column, height: 100 }
      }
      let left = {
        pos: { x: -100, y: 0 },
        size: { width: 100, height: this.globleConfig.column }
      }



      return [...this.componentList, bottom, right, top, left].filter(v => {
        if (component === v) { return; }
        let wa = component.size.width;
        let wb = v.size.width;
        let ha = component.size.height;
        let hb = v.size.height;
        let xa = component.pos.x + wa / 2;
        let ya = component.pos.y + ha / 2;
        let xb = v.pos.x + wb / 2;
        let yb = v.pos.y + hb / 2;

        return Math.abs(xb - xa) < wa / 2 + wb / 2 && Math.abs(yb - ya) < ha / 2 + hb / 2
      })
    },
    setValidState (component, valid) {
      const traversal = c => {
        c.valid = valid
        c.children.forEach(traversal)
      }
      traversal(component);
    },
    generateInteractInstance (component, { fromLoad }) {
      let stateBeforeTransform = {};
      let hasMoved = fromLoad;

      this.$nextTick(() => {
        let element = document.getElementById('component-' + component.dId)
        component._dom = element;
        element._component = component;
        this.interact(element)
          .draggable({
            modifiers: [
              this.interact.modifiers.snap({
                targets: [
                  this.interact.createSnapGrid({ x: this.gridWidth, y: this.gridHeight })
                ],
                range: Infinity,
                relativePoints: [ { x: 0, y: 0 } ]
              }),
            ],
            inertia: false,
          })
          .resizable({
            // resize from all edges and corners
            edges: { left: true, right: true, bottom: true, top: true },

            modifiers: [
              // minimum size
              this.interact.modifiers.restrictSize({
                min: { width: 100, height: 50 },
              }),

              
              this.interact.modifiers.snapSize({
                targets: [
                  this.interact.createSnapGrid({ x: this.gridWidth, y: this.gridHeight })
                ],
                range: Infinity,
                relativePoints: [ { x: 0, y: 0 } ]
              }),
            ],          

            inertia: false
          })
          .on('dragmove', (event) => {
            hasMoved = true;
            let dx = Math.round(event.dx / this.gridWidth);
            let dy = Math.round(event.dy / this.gridHeight);
            
            const setState = c => {
              c.pos.x += dx;
              c.pos.y += dy;
              this.updateComponent(c);
              c.children.forEach(setState)
            }
            setState(component)

            this.setValidState(component, this.conflictCheck(component).length === 0)
          })
          .on('resizemove', (event) => {
            let w = Math.round(event.rect.width / this.gridWidth)
            let h = Math.round(event.rect.height / this.gridHeight)

            let _special = false
            let x = component.pos.x
            let y = component.pos.y
            // 如果这个组件有父元素的话  则不能超出父元素
            if (component.parent) {
              _special = true;
              if (event.edges.top) {
                y = component.pos.y + (component.size.height - h)
                if (y < component.parent.pos.y) {
                  h += y - component.parent.pos.y
                  y = component.parent.pos.y
                }
              }
              if (event.edges.left) {
                x = component.pos.x + (component.size.width - w)
                if (x < component.parent.pos.x) {
                  w += x - component.parent.pos.x
                  x = component.parent.pos.x
                }
              }
              if (event.edges.bottom) {
                h = Math.min(h, component.parent.size.height + component.parent.pos.y - component.pos.y)
              }
              if (event.edges.right) {
                w = Math.min(w, component.parent.size.width + component.parent.pos.x - component.pos.x)
              }
            } 
            if (component.children.length > 0) {
              _special = true;
              let max = this.getComponentsRect(component.children);
              if (event.edges.top) {
                y = component.pos.y + (component.size.height - h)
                if (y > max[0]) {
                  h += y - max[0]
                  y = max[0]
                }
              }
              if (event.edges.left) {
                x = component.pos.x + (component.size.width - w)
                if (x > max[3]) {
                  w += x - max[3]
                  x = max[3]
                }
              }
              if (event.edges.bottom) {
                h = Math.max(h, max[2] - component.pos.y)
              }
              if (event.edges.right) {
                w = Math.max(w, max[1] - component.pos.x)
              }
            } 
                component.pos.y = y
                component.pos.x = x

            if (!_special) {

              if (event.edges.top) {
                let y = component.pos.y + (component.size.height - h)
                component.pos.y += (component.size.height - h)
              }
              if (event.edges.left) {
                component.pos.x += (component.size.width - w)
              }
              
            }

            

            component.size.width = w;
            component.size.height = h;


            this.updateComponent(component);

            this.setValidState(component, this.conflictCheck(component).length === 0)
          })
          .on(['dragstart', 'resizestart'], event => {
            stateBeforeTransform = {};
            let _stateBeforeTransform = stateBeforeTransform;
            const copy = (_component, _stateBeforeTransform) => {
              _stateBeforeTransform.pos = Object.assign({}, _component.pos)
              _stateBeforeTransform.size = Object.assign({}, _component.size)
              _stateBeforeTransform.offset = _component.offset
              _stateBeforeTransform.parent = _component.parent
              _stateBeforeTransform.children = [];
              _component.children.forEach(v => {
                let obj = {};
                _stateBeforeTransform.children.push(obj);
                copy(v, obj);
              })
            }
            copy(component, stateBeforeTransform);
            const setState = (c) => {
              c.zIndex = 10 + c.originZIndex
              c.state = 'transforming'
              c.children.forEach(setState)
            }
            setState(component)
          })
          .on(['dragend', 'resizeend'], event => {
            // 保证onDrop事件先执行  所以延迟一会儿.
            this.$nextTick(() => {
              if (!component.valid && !component.isDropping) {
                if (stateBeforeTransform.pos.x < 0 && stateBeforeTransform.pos.y < 0) { 
                  this.deleteComponent(component); 
                  return; 
                }
                const paste = (_component, _stateBeforeTransform) => {
                  _component.pos.x = _stateBeforeTransform.pos.x;
                  _component.pos.y = _stateBeforeTransform.pos.y;
                  _component.size.width = _stateBeforeTransform.size.width;
                  _component.size.height = _stateBeforeTransform.size.height;
                  _component.offset = _stateBeforeTransform.offset;
                  _component.parent = _stateBeforeTransform.parent;
                  _component.parent && !_component.parent.children.find(v => v === _component) && _component.parent.children.push(_component)
                  _stateBeforeTransform.children.forEach((v, i) => {
                    paste(_component.children[i], v)
                  })
                }
                paste(component, stateBeforeTransform);

                this.updateComponent(component)
                this.setValidState(component, true)
              }
              const setState = c => {
                c.zIndex = c.originZIndex
                c.state = 'inPage'
                c.children.forEach(setState)
              }
              setState(component)
            })
          });
          element.style.transform = 'translate(0px, 0px)';


        this.updateComponent(component);
        if (!fromLoad) {
          element.dispatchEvent(new PointerEvent('pointerdown', {
            bubbles: true,
            clientX: 50,
            clientY: 120,
            pointerType: "mouse",
          }))
          setTimeout(() => {
            if (!hasMoved) { this.deleteComponent(component) }
          }, 200);
        }
      })
    },
    initComponent (component) {
      let defaultValue = {
        dId: component.id + '-' + Math.floor(Math.random() * 1000),
        valid: true,
        isDropping: false,
        zIndex: 1,
        originZIndex: 1,
        pos: {x: -1, y: -1},
        state: 'standBy',
      }
      for (let i in defaultValue) {
        component[i] = defaultValue[i]
      }
      return component
    },
    createBackgroundPattern () {
      return new Promise(rev => {
          
        let canvas = document.createElement('canvas');
        let ctx = canvas.getContext('2d');
        canvas.width = 2;
        canvas.height = 2;
        ctx.clearRect(0, 0, 2, 2);
        ctx.fillStyle = 'rgba(255,255,255,0.05)'
        ctx.fillRect(0,0,1,1)
        ctx.fillRect(1,1,1,1)

        canvas.toBlob(blob => {
          let fr = new FileReader();
          fr.onload = function (e) {
            rev(e.target.result);
          }
          fr.readAsDataURL(blob);
        })

      })

    },
    startDragFromStandByPanel (e) {
      let component = this.initComponent({
        id: (new Date()).getTime() + '-' + Math.floor(Math.random() * 1000),
        size: {width: 4, height: 4},
        config: null,
        outputs: [],
        inputs: [],
        children: [],
        actionUrl: null,
        parent: null,
      })

      this.componentList.push(component)
      this.generateInteractInstance(component, {});
    },
    deleteComponent (component) {
      this.interact(component._dom).unset()
      this.componentList.splice(this.componentList.indexOf(component), 1)
    },
    exportScene () {
      const map = v => {
        let c = Object.assign({}, v);
        delete c._dom;
        delete c.dId;
        return c;
      }
      let componentList = this.componentList.map(map)
      return componentList;
    },
    save () {
      this.sceneList[this.curView].save = this.exportScene();
      localStorage.setItem('customReportSave', JSON.stringify(this.sceneList));
    },
    load () {
      let save = localStorage.getTime('customReportSave') || '[]'
      save = JSON.parse(save)
      this.sceneList = save;
    },
    changeTab (to) {
      this.sceneList[this.lastView].save = this.exportScene();
      this.componentList.forEach(v => this.interact(v._dom).unset());
      this.componentList = this.sceneList[to.name].save || [];
      this.componentList.forEach(v => {
        this.generateInteractInstance(v, {fromLoad: true})
      })
      this.lastView = to.name;
    }
  },
  mounted () {
    
    this.$nextTick(() => {
      this.__onWindowResize = () => {
        this.globleConfig.contentWidth = this.contentPanelDom.offsetWidth;
        this.globleConfig.contentHeight = this.contentPanelDom.offsetHeight;
      }
      window.addEventListener('resize', this.__onWindowResize);
      this.contentPanelDom = document.querySelector('#custom_report_page .page-view')
  
      this.__onWindowResize();
      this.createBackgroundPattern().then(res => {
        this.contentPanelDom.style = `background-image: url(${res}); background-size: ${200 / this.globleConfig.column}% ${200 / this.globleConfig.row}%;`
      });
    })
  },
  beforeDestory () {
    window.removeEventListen('resize', this.__onWindowResize);
  }
}
</script>

<style lang="less" scoped>
.wrapper{
  position: relative;
  height: 100%;
  height: calc(100% - 10px);
  margin-left: 10px;
  margin-right: 10px;
  background-color: #0B1D3B;
}
.page-view{
  flex: 1;
  background-color: transparent;
  image-rendering: pixelated;
  margin-right: 200px;
  .component{
    width: 20%;
    height: 200% / 12;
    // background-color: #2299ee55;
    color: #fff;
    font-size: 1.2em;
    border-radius: 4px;
    touch-action: none;
    box-sizing: border-box;
    position: absolute;
    opacity: 1;
    border: 2px #0d5f99 solid;
    transition: background-color 0.2s, opacity 0.2s;
  }
}
.stand-by-panel{
  position: absolute;
  right: 0px;
  width: 200px;
  bottom: 0px;
  top: 40px;
  background-color: #6ff;
  .component{
    width: 100px;
    height: 100px;
    background-color: #f00;
  }
}
</style>


